import Masonry from 'react-masonry-css'

import { Box, Card, Typography } from '@mui/material'

import { type ClubId } from 'src/models/club'

import ReviewCard from 'src/components/ReviewCard'

import { useGetClubReviews } from 'src/hooks/api/review'

const breakpointColumns = {
  default: 3,
  960: 2,
  600: 1
}

interface ReviewGridProps {
  clubId: ClubId
  name?: string
  description?: string
}

const ReviewGrid: React.FC<ReviewGridProps> = ({ clubId, name, description }) => {
  const { data: reviews } = useGetClubReviews(clubId)

  return reviews && reviews.length > 0 ? (
    <Box
      component={Masonry}
      breakpointCols={breakpointColumns}
      sx={{
        display: 'flex',
        marginLeft: '-16px',
        width: 'auto',
        '& > div': {
          paddingLeft: '16px',
          backgroundClip: 'padding-box'
        }
      }}
    >
      {reviews.map(review => (
        <Box key={review.id} mb={2}>
          <ReviewCard {...review} name={name} description={description} />
        </Box>
      ))}
    </Box>
  ) : (
    <Card sx={{ p: 2 }}>
      <Typography color="text.secondary">아직 작성된 후기가 없어요.</Typography>
    </Card>
  )
}

export default ReviewGrid
